/* HOME */

  /* TRansition HOME */
  .example-appear {
    opacity: 0.01;
  }

  .example-appear.example-appear-active {
    opacity: 3;
    transition: opacity 5s ease-in;
  }

 .HOME{
    display: grid;
    grid-template-areas: "HOME-background-nav"
                "HOME-background-job-brand"
                "HOME-background-content-title"
                "HOME-background-content-nameOwner"
                "Home-scrolling"; 
    background-image: linear-gradient(180deg, white 40%, black 60%);
    cursor: default; 
    -webkit-columns: #000000; 
            columns: #000000;;
    justify-items: center;
    text-align: center;
    align-items: center;
    grid-template-rows: 0.1fr repeat(auto-fill, minmax(100px, 0.1fr));
    grid-gap: 2%;
  }
.HOME-background{
  background-color: rgb(0, 0, 0);
  opacity: 0.5;
  color: whitesmoke;
  padding: 1% 1% 1% 1%; 
}
#HOME-background-nav{
  grid-area: HOME-background-nav; 
  display: flex;
  justify-content: space-around;
}

.HOME-background-job-brand{
  grid-area: HOME-background-job-brand;
  font-size: 30px;
}
.HOME-background-content-title{
   grid-area: HOME-background-content-title; 
   font-size: 75px;
}
.HOME-background-content-nameOwner{
  grid-area:HOME-background-content-nameOwner; 
  display: flex;
  justify-content: space-around;
  font-size: 20px;
}

.Home-scrolling{
  grid-area: Home-scrolling; 
  display: flex;
  justify-content: center;
}

#HOME-background-nav-logo{
  height: 50px;
  width: auto;
}
#HOME-background-nav-logo:hover{
  transform: rotate(0.5turn); 
}


.HOME-background-content-info-info-list:hover{
  border: 2px white solid;
}


.Home-scrolling-shape{
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: 1px white solid; 
}
.Home-scrolling-shape:hover{
    background-image: linear-gradient(180deg, gray 40%, black 60%);
    border: none;
}


